import { useDispatch } from 'react-redux';

import { uiHiddenCardsEnterprises } from '../../actions/ui';
import { startActiveEmpresa } from '../../actions/auth';
import iconEnterprises from '../../assets/iconEnterprises.svg';
import arrowRight from '../../assets/arrowRight.svg';

const EmpresasCard = ({ ...enterprise }) => {
    
    const dispatch = useDispatch();

    const handleEmpresaSelect = () => {
        dispatch( uiHiddenCardsEnterprises() );
        dispatch( startActiveEmpresa( enterprise.codigo ) );
    }

    return (
        <div 
            className="empresa-card"
            onClick={ handleEmpresaSelect }
        >

            <section className="header-card">
                <img src={iconEnterprises} className="icon-empresa" alt="Ícono de la empresa"/>
                <h1>{enterprise.nombre.trim()}</h1>
            </section>

            <section className="title-card">
                <h2>{enterprise.ruc.trim()}</h2>
            </section>

            <section className="body-card">
                <p>{enterprise.email.trim()}</p>
                <p>{enterprise.direccion.trim()}</p>
                <div className="button-card"><img src={arrowRight} alt="Flecha" /></div>
            </section>
        </div>
    )
}

export default EmpresasCard;
